export interface IWedDiffMargin extends IElementSkAnnot {
	skAnnot: ISkDiffAnnot

	/** Wedlet dans lequel cette margin est placée. */
	wedlet: IWedlet

	eraseDiffLabel: string

	/** Elimine la différence, ie, en fonction du mode, en acceptant la contribution tierce ou annulant les écarts. */
	eraseDiff(): void

	removeDiffWidget(): void

	/** Pour ajouter le diff des metas correspondant à un tag marqué (WedDiffWrapMark, WedDiffTxtObj). */
	addAnnotForMetas?(metaAnnot: ISkDiffAnnot): void

	removeAnnotForMetas?(metaAnnot: ISkDiffAnnot): void
}


/**
 * Indicateur d'annotation de diff insérée dans un ElementWedlet (margin dans son shadow).
 */
export class WedDiffMark extends HTMLElement implements IWedDiffMargin, IContextMenuActionsPointer<IWedDiffMargin> {
	skAnnot: ISkDiffAnnot;
	holder: HTMLElement & IWedAnnotPointer;

	foreignReplace?: WedDiffForeign;

	get wedlet(): IWedlet {return findElementWedlet(this.holder)?.wedlet}

	initDiffAnnot(holder: HTMLElement & IWedAnnotPointer, skAnnot: ISkDiffAnnot, wedlet: IWedlet, foreignReplace?: WedDiffForeign, noAction?: boolean): this {
		const wedMgr = wedlet.wedMgr;
		this.holder = holder;
		this.foreignReplace = foreignReplace;
		//this.tabIndex = 0;
		holder.wedAnnotDiff = this;
		wedMgr.reg.installSkin(this.localName, this.attachShadow(DOMSH.SHADOWDOM_INIT));
		this.skAnnot = skAnnot;
		this.title = foreignReplace ? skAnnot.diffSession.mode === EXmlDiffMode.diff ? "Contenu issu d'un remplacement" : "Contenu éliminé par un remplacement" : skAnnot.getLabel();
		const uiType = skAnnot.level.name;
		this.classList.add(uiType);
		if (skAnnot instanceof DiffAnnotReplace) {
			const uiSubType = skAnnot.inCurrent.level.name;
			this.classList.add(uiSubType);
			holder.setAttribute("annot-diff", uiSubType);
		} else {
			holder.setAttribute("annot-diff", uiType);
		}
		if (!noAction) this.shadowRoot.appendChild(<ActionBtn î={{reg: wedMgr.reg, action: ERASEDIFF, actionContext: this, uiContext: 'bar'} as OActionBtnInit<IWedDiffMargin>}/>);
		return this;
	}

	/** Insertion par défaut. */
	defaultInject(): this {
		DOM.setStyle(this.holder, "position", "relative");
		(this.holder.shadowRoot || this.holder).appendChild(this);
		return this;
	}

	removeDiffWidget() {
		this.holder.wedAnnotDiff = null;
		this.holder.removeAttribute("annot-diff");
		this.remove();
		this.holder = null;
	}

	get ctxMenuActions(): IContextMenuActions<IWedDiffMargin> {
		return {
			actions: [ERASEDIFF],
			actionContext: this
		};
	}

	get eraseDiffLabel() {
		if (this.foreignReplace) return this.skAnnot.diffSession.mode === EXmlDiffMode.diff ? "Annuler ce remplacement" : "Valider ce remplacement par l'autre version";
		return this.skAnnot.diffSession.mode === EXmlDiffMode.diff ? "Annuler cet ajout" : "Valider cette suppression";
	}

	eraseDiff() {
		const batch = this.fillBatch();
		if (this.foreignReplace && this.foreignReplace.parentNode) {
			batch.needAdjustForNextAdds();
			this.foreignReplace.fillBatch(batch);
			this.foreignReplace.removeDiffWidget();
		}
		this.removeDiffWidget();
		batch.doBatch();
	}

	fillBatch(batch?: IDocBatch & XmlBatch): IDocBatch & XmlBatch {
		const eltWedlet = findElementWedlet(this.holder);
		if (eltWedlet) {
			if (!batch) batch = eltWedlet.wedlet.wedMgr.docHolder.newBatch();
			const xa = eltWedlet.wedlet.wedAnchor;
			if (XA.isAttribute(xa)) {
				batch.setAttr(xa, null);
			} else {
				batch.deleteSequence(xa, 1);
			}
		}
		return batch;
	}
}

