/** Marque pour les balises inlines wrappées. */
export class WedDiffWrapMark extends WedDiffMark {
	startMark: WedDiffWrapMark;

	initDiffWrapAnnot(holder: InlElement, skAnnot: IDiffAnnotMark, wedlet: IWedlet, startMark?: boolean): this {
		const wedMgr = wedlet.wedMgr;
		wedMgr.reg.installSkin(this.localName, this.attachShadow(DOMSH.SHADOWDOM_INIT));
		this.skAnnot = skAnnot;
		this.title = skAnnot.getLabel();
		this.classList.add(skAnnot.level.name);
		if (startMark) {
			this.shadowRoot.append("[");
			holder.insertBefore(this, holder.firstChild);
		} else {
			this.holder = holder;
			holder.wedAnnotDiff = this;
			this.shadowRoot.append("]");
			holder.appendChild(this);
			if (skAnnot.wrap === "full") this.startMark = new WedDiffWrapMark().initDiffWrapAnnot(holder, skAnnot, wedlet, true);
		}
		return this;
	}

	addAnnotForMetas(metaAnnot: ISkDiffAnnot): void {
		//TODO pour ajouter les metas quand impl du eraseDiff() interne au para.
	}

	removeAnnotForMetas(metaAnnot: ISkDiffAnnot): void {
		//TODO pour ajouter les metas quand impl du eraseDiff() interne au para.
	}

	removeDiffWidget() {
		this.holder.wedAnnotDiff = null;
		if (this.startMark) {
			this.startMark.remove();
			this.startMark = null;
		}
		this.remove();
		this.holder = null;
	}

	initDiffAnnot(holder: HTMLElement & IWedAnnotPointer, skAnnot: ISkDiffAnnot, wedlet: IWedlet, foreignReplace?: WedDiffForeign, noAction?: boolean): this {
		throw Error("do not use");
	}

	defaultInject(): this {
		throw Error("do not use");
	}

	eraseDiff() {
		//TODO
	}
}

