/**
 * Elément affichant un contenu issu de la source étrangère dans un sous-éditeur dans les modes diff et patch.
 * Gère aussi le diff de type remplacement d'un autre élément.
 * Doit être inséré en élément frère des wedlets au point d'insertion correspondant.
 */
export class WedDiffForeign extends HTMLElement implements IWedDiffMargin, IContextMenuActionsPointer<IWedDiffMargin> {
	skAnnot: IDiffAnnotForeign;
	margin: HTMLElement;

	/** Construction d'un editor affichant le fragment (IDiffAnnotForeign) */
	subEditor?: WedEditorFrag;

	/** Gestion du remplacement. */
	replaceOther?: WedDiffMark;

	get wedlet(): IWedlet {return findElementWedlet(this)?.wedlet}

	initDiffForeign(annot: IDiffAnnotForeign, house: XmlTypedHouse, parentWedlet: IWedletSingleElt, replaceOther?: WedDiffMark): this {
		this.skAnnot = annot;
		this.replaceOther = replaceOther;
		const parentWedMgr = parentWedlet.wedMgr;
		const sr = this.attachShadow(DOMSH.SHADOWDOM_INIT);
		parentWedMgr.reg.installSkin(this.localName, sr);
		this.classList.add(annot.level.name);
		const foreignModel = parentWedlet.model.buildModelForFragment(parentWedlet);
		if (foreignModel) {
			//Foreign doc
			const docHolder = new DocHolder(house);
			this.subEditor = new WedEditorFrag().initialize({reg: parentWedMgr.reg, commonBar: (parentWedMgr.wedEditor as IWedEditorCommonBar).commonBar} as IWedEditorCommonBar);
			this.subEditor.wedModel = parentWedMgr.wedModel;
			//Init de l'éditeur
			this.subEditor.initFromDocHolder(docHolder, {noAnnots: true, readOnlyCauses: ["diffForeign"], rootModel: foreignModel, xaRoot: annot.attName ? [0] : undefined});
			this.shadowRoot.appendChild(this.subEditor);
		} else {
			this.shadowRoot.append("Différence non affichable");
		}
		//Ajout de la margin
		this.margin = sr.appendChild(<div class="margin" title={replaceOther ? annot.diffSession.mode === EXmlDiffMode.diff ? "Contenu éliminé par un remplacement" : "Contenu issu d'un remplacement" : annot.getLabel()}/>);
		this.margin.classList.add(annot.level.name);
		if (replaceOther) this.margin.classList.add("replace");
		this.margin.addEventListener("pointerdown", this.onPointerdown);
		this.margin.appendChild(<ActionBtn î={{reg: parentWedMgr.reg, action: ERASEDIFF, actionContext: this, uiContext: 'bar'} as OActionBtnInit<IWedDiffMargin>}/>);
		return this;
	}

	//get stopBoxSelection(): true {return true}

	get ctxMenuActions(): IContextMenuActions<IWedDiffMargin> {
		return {
			actions: [ERASEDIFF],
			actionContext: this
		};
	}

	removeDiffWidget() {
		this.remove();
	}

	get eraseDiffLabel() {
		if (this.replaceOther) return this.skAnnot.diffSession.mode === EXmlDiffMode.diff ? "Rétablir ce contenu remplacé" : "Valider ce remplacement";
		return this.skAnnot.diffSession.mode === EXmlDiffMode.diff ? "Annuler cette suppression" : "Valider cet ajout";
	}

	eraseDiff() {
		//console.log(":::REVERT:::", this);
		const batch = this.fillBatch();
		if (this.replaceOther) {
			batch.needAdjustForNextAdds();
			this.replaceOther.fillBatch(batch);
		}
		this.remove();
		batch.doBatch();
	}

	fillBatch(batch?: IDocBatch & XmlBatch): IDocBatch & XmlBatch {
		const parent = DOM.findParent(this, null, IS_EltWedlet);
		if (!batch) batch = parent.wedlet.wedMgr.docHolder.newBatch();
		if (this.skAnnot.attName) {
			batch.setAttr(this.skAnnot.start, this.skAnnot.strValue);
		} else {
			batch.insertJml(this.skAnnot.start, JML.dom2jml(this.skAnnot.foreignNode));
		}
		return batch;
	}

	focus(options?: FocusOptions): void {
		const root = this.subEditor.rootNode;
		const elt = DOMSH.findFlatNext(root, root, IS_focusable);
		if (elt) elt.focus(options);
	}

	onPointerdown(this: HTMLDivElement, ev: Event) {
		DOMSH.findHost<WedDiffForeign>(this).focus();
		ev.preventDefault();
	}
}

