/**
 * Elément affichant une valeur issue du document étranger.
 * Cet élément doit être inséré dans le shadow de l'elementWedlet de type input (ie wedlet bind de type text attribute ou comment).
 */
export class WedDiffValue extends HTMLElement implements IWedDiffMargin, IContextMenuActionsPointer<IWedDiffMargin>, IBoxStopSelection {
	skAnnot: IDiffAnnotValue;
	holder: IElementWedlet & IWedAnnotPointer;
	margin: HTMLElement;

	get wedlet(): IWedlet {return findElementWedlet(this)?.wedlet}

	get stopBoxSelection(): true {return true};

	initDiffValue(annot: IDiffAnnotValue, holder: IElementWedlet & IWedAnnotPointer, wedMgr: WedMgr, inputWidget?: HTMLElement): this {
		this.skAnnot = annot;
		this.holder = holder;
		holder.wedAnnotDiff = this;
		this.holder.setAttribute("annot-diff", annot.level.name);
		const sr = this.attachShadow(DOMSH.SHADOWDOM_INIT);
		wedMgr.reg.installSkin(this.localName, sr);
		this.classList.add(annot.level.name);
		sr.appendChild(document.createElement("slot"));
		this.margin = sr.appendChild(<div class="margin" title={annot.getLabel()}/>);
		this.margin.appendChild(<ActionBtn î={{reg: wedMgr.reg, action: ERASEDIFF, actionContext: this, uiContext: 'bar'} as OActionBtnInit<IWedDiffMargin>}/>);
		this.appendChild(inputWidget || <div>{annot.otherValue}</div>);
		const p = holder.shadowRoot?.getElementById("diffCtn") || holder.shadowRoot || holder;
		p.insertBefore(this, p.firstChild);
		return this;
	}

	//get stopBoxSelection(): true {return true}

	get ctxMenuActions(): IContextMenuActions<IWedDiffMargin> {
		return {
			actions: [ERASEDIFF],
			actionContext: this
		};
	}

	get eraseDiffLabel() {
		return this.skAnnot.diffSession.mode === EXmlDiffMode.diff ? "Rétablir cette valeur" : "Valider cette valeur";
	}

	removeDiffWidget() {
		this.holder.wedAnnotDiff = null;
		this.holder.removeAttribute("annot-diff");
		this.remove();
		this.holder = null;
	}

	eraseDiff() {
		const wedlet = this.holder.wedlet;
		wedlet.wedMgr.docHolder.newBatch()
			.setText(wedlet.wedAnchor, this.skAnnot.otherValue)
			.doBatch();
		this.removeDiffWidget();
	}
}

