export class WedDiffTable extends HTMLElement {
	table: TxtTable;

	annots: ISkDiffAnnot[];

	initTable(table: TxtTable): this {
		const sr = this.attachShadow(DOMSH.SHADOWDOM_INIT);
		table.wedMgr.reg.installSkin(this.localName, sr);
		this.shadowRoot.append(<div/>);
		this.table = table;
		table.insertAdjacentElement("beforebegin", this);
		table.classList.toggle("diff", true);
		//TODO init d'un foreignEditor avec le contenu de table en cours (expurgé du contenu des cells ?).
		this.annots = [];
		return this;
	}

	addAnnot(annot: ISkDiffAnnot) {
		this.annots.push(annot);
		//TODO
		// - traduction de annot en msg partant de la table en root.
		// - adjust du msg avec les msgs précédents.
		// - application de ce msg au foreignEditor de ce WedDiffTable
		// - post-balisage de ces structs insérées en mode inverse de annot.diffSession.mode
		this.shadowRoot.lastElementChild.textContent = `Des modifications de la structure du tableau ne sont pas représentées`;
	}

	removeAnnot(annot: ISkDiffAnnot) {
		//Aujourd'hui pas de suppr d'annot partielles, donc on vire tout ce bloc dès le 1er appel au remove d'une annot.
		this.table.diffTable = null;
		this.table.classList.toggle("diff", false);
		this.remove();
	}
}

