/**
 *
 */
export class WedDiffPara extends HTMLElement implements IWedDiffMargin {
	subEditor: WedEditorFrag;
	skAnnot: ISkDiffAnnot;

	get wedlet(): IWedlet {return findElementWedlet(this)?.wedlet}

	get eraseDiffLabel() {return this.skAnnot.diffSession.mode === EXmlDiffMode.diff ? "Annuler toutes les modifications de ce paragraphe" : "Valider toutes les modifications de ce paragraphe"}

	initDiffPara(para: TxtParaLike, annot: IDiffAnnotMixCt): this {
		//console.log(":::setDiffOnPara:::",annot);
		this.skAnnot = annot;
		const wedMgr = para.wedMgr;
		const sr = this.attachShadow(DOMSH.SHADOWDOM_INIT);
		wedMgr.reg.installSkin(this.localName, sr);
		sr.appendChild(<ActionBtn î={{reg: wedMgr.reg, action: ERASEDIFF, actionContext: this, uiContext: 'bar'} as OActionBtnInit<IWedDiffMargin>}/>);
		//Foreign doc
		const foreignDoc = DOM.newDomDoc() as IDocumentNs;
		foreignDoc.namespaces = wedMgr.docHolder.house.namespaces;
		mixCtToForeign(annot.diffRoot, JML.jmlToDom([para.paraModel.newJml()], foreignDoc).firstElementChild);
		//Foreign house
		const house = new XmlTypedHouse({
			initialDoc: foreignDoc,
			schema: annot.skNodePara.schemaDom.schema,
			schemaDomConfig: {rootRule: new SkRuleDoc().init(new SkRuleElt().init(SkMatcherAnyElt.SINGLETON, '1', annot.skNodePara.rule.contentRule).initSkMeta(annot.skNodePara.rule.skMeta))},
			buildOptions: {autoMutate: true, autoComplete: true, autoNormXml: true, autoNormChars: true, genAnnots: false}
		});
		const docHolder = new DocHolder(house);
		const subEditor = this.subEditor = new WedEditorFrag().initialize({reg: wedMgr.reg, commonBar: (wedMgr.wedEditor as IWedEditorCommonBar).commonBar} as IWedEditorCommonBar);
		subEditor.wedModel = wedMgr.wedModel;
		//Init de l'éditeur
		const foreignModel = para.model.buildModelForFragment(para);
		subEditor.initFromDocHolder(docHolder, {readOnlyCauses: ["diffForeign"], rootModel: foreignModel});
		subEditor.wedMgr.listeners.on("redrawAtEnd", para.diffForeignRedrawAtEnd);
		subEditor.wedMgr.listeners.on("redrawAtEnd", (subWedMgr: WedMgr) => {
			subWedMgr.docHolder.setDiffSession(annot.paraSession);
		});
		//
		para.insertAdjacentElement("afterbegin", subEditor);
		para.insertAdjacentElement("beforeend", this);
		para.setAttribute("diff-para", "");
		para.style.position = "relative"; //Pour txt-member, txt-caption qui n'est pas display:block.
		return this;
	}

	eraseDiff(): void {
		const eltWedlet = findElementWedlet(this) as TxtParaLike;
		const replaceCt = this.subEditor.wedMgr.docHolder.getContent();
		replaceCt[0] = eltWedlet.newJml(); //On remplace le tag racine (cas des sc:caption et sc:member remplacés par des sc:para dans le foreign)
		eltWedlet.wedlet.wedMgr.docHolder.newBatch().spliceSequence(eltWedlet.wedlet.wedAnchor, 1, replaceCt).doBatch();
		this.removeDiffWidget();
	}

	removeDiffWidget(): void {
		if (this.parentElement) {
			this.parentElement.removeAttribute("diff-para");
			DOM.findFirstChild(this.parentNode, (n: Node): n is WedEditorFrag => n instanceof WedEditorFrag)?.remove();
			this.remove();
		}
	}

}

